.social {
  @include reset-list;

  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
}


.social__item {
  margin: 0 6px;
}


.social__link {
  display: block;
  width: 51px;
  height: 51px;
  padding-top: 12px;

  border: 3px solid $color--primary-10;
  border-radius: 50%;


  svg {
    fill: $color--brand;
  }


  &:hover,
  &:focus {
    border: 3px solid $color--primary;
  }


  &:active {
    border: 3px solid $color--primary-10;

    svg {
      fill: $color--brand-30;
    }
  }
}
